import React from 'react';
import {connect} from "react-redux";
import {IconPack} from "../../../../Images/IconPack";

import InputLabel from "@material-ui/core/InputLabel/InputLabel";

import TextField from "@material-ui/core/TextField/TextField";
import { FormattedMessage } from 'react-intl';

import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';


const CorporateActivity = (props) => {
    const [searchValue, setSearchValue] = React.useState('');

    const getDayRow = (type) => {
        let date = new Date();
        var month = ["Jan", "Feb", "Mar", "Apr", "May", "Jun",
            "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"][date.getMonth()];
        let day;


        switch (type) {
            case 'Today':
                day = date.getDate();
                break;
            case 'Yesterday':
                day = date.getDate() - 1;
                break;
            default:
                    break;
        }


       return (<div className="day-row">
                <b className="color-white">{type}</b>
                <span className="color-grey inline">{day} {month}</span>
       </div>);
    }




    const getTbodyData = () => {
        return (<>
            {getDayRow('Today')}
            <div className="color-grey mb-10">User / Action</div>
            {props.activityData.map((i, key) => {
                return  (
                    <Accordion >
                        <AccordionSummary
                            aria-controls={"panel"+key+"a-content"}
                            id={"panel"+key+"a-header"}
                        >
                            <span className="icon-inside align-items-baseline inline mr-10 bold">{IconPack.userIcons.forTable}{i.user}</span><span className="bold">{i.action}</span>
                        </AccordionSummary>
                        <AccordionDetails>
                            <span className="accordion-data-description">Description</span>
                            {i.description[0]}<span className="color-grey icon-inside align-items-baseline inline ml-10">{IconPack.licenseIcons.mail}{i.description[1]}</span>
                            <span className="accordion-data-description">Date</span>
                             13 Feb, 11:10:22
                        </AccordionDetails>
                    </Accordion>
                );
            })}

            {getDayRow('Yesterday')}

            {props.activityData.map((i, key) => {
                return  (    <Accordion>
                        <AccordionSummary
                            aria-controls={"panel"+key+"a-content"}
                            id={"panel"+key+"a-header"}
                        >
                            <span className="icon-inside align-items-baseline inline mr-10 bold">{IconPack.userIcons.forTable}{i.user}</span><span className="bold">{i.action}</span>
                        </AccordionSummary>
                        <AccordionDetails>
                            <span className="accordion-data-description">Description</span>
                            {i.description[0]}<span className="color-grey icon-inside align-items-baseline inline ml-10">{IconPack.licenseIcons.mail}{i.description[1]}</span>
                            <span className="accordion-data-description">Date</span>
                            13 Feb, 11:10:22
                        </AccordionDetails>
                    </Accordion>
                );
            })}

        </>);
    };



    let searchedData = props.activityData.filter(i => {
        if (i.user.toLowerCase().includes(searchValue.toLowerCase()) ||
            i.description[0].toLowerCase().includes(searchValue.toLowerCase())||
            i.description[1].toLowerCase().includes(searchValue.toLowerCase())||
            i.action.toLowerCase().includes(searchValue.toLowerCase())) {
            return i
        }
    })

    searchedData = searchedData.map((i, key) => {

          return  ( <Accordion>
              <AccordionSummary
                  aria-controls={"panel"+key+"a-content"}
                  id={"panel"+key+"a-header"}
              >
                  <span className="icon-inside align-items-baseline inline mr-10">{IconPack.userIcons.forTable}{i.user}</span>{i.action}
              </AccordionSummary>
              <AccordionDetails>
                  <span className="accordion-data-description">Description</span>
                  {i.description[0]}<span className="color-grey icon-inside align-items-baseline inline ml-10">{IconPack.licenseIcons.mail}{i.description[1]}</span>
                  <span className="accordion-data-description">Date</span>
                  13 Feb, 11:10:22
              </AccordionDetails>
          </Accordion>);
    });




    return (
        <div className="ÑustomStyle">
                <div className="filterPanel filterPanel--mobile ">
                    <FormattedMessage  id='filterPanel.search'>
                        {(msg) => ( <TextField
                            variant="filled"
                            fullWidth
                            className="search_area"
                            placeholder={msg}
                            value={searchValue}
                            onChange={(e)=>{setSearchValue(e.target.value)}}
                        >
                        </TextField>)}
                    </FormattedMessage>

                    <div className="search_additional_components">
                        <div className="calendar">{IconPack.searchComponents.calendar}</div>
                    </div>
                </div>

            {searchValue.length ? searchedData : getTbodyData() }

        </div>
    );
}

const mapStateToProps = (state) => ({
    activityData: state.corporateLicenseActivity.activity,
});


export default connect(mapStateToProps)(CorporateActivity);